@import "normalize";
@import "variables";
@import "mixins";

/*HEADER*/

:root {
  --blue-: #46B1DE;
}

* {
  font-family: 'Montserrat', sans-serif;
  font-family: 'Roboto', sans-serif;
  margin-left: 0;
  margin-right: 0;
}

body {
  font-family: 'Roboto';
  font-weight: 400;
}

@font-face {
  font-family: 'Sofia';
  src: url('../fonts/Sofia.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

/*меню*/
header {
  position: absolute;
  padding: 20px 0;
  top: 0;
  left: 50%;
  width: 100%;
  transform: translate(-50%, 0);
  z-index: 1;
}

.container {
  .menu {
    .logo {
      display: flex;
      align-items: center;
    }
  }
}

/*mixin*/
svg {
  fill: #46B1DE;
  z-index: 100;
}

.container {
  .menu {
    display: flex;
    margin-bottom: 34px;
  }
}

.menu {
  nav {
    .menu-text {
      display: flex;
      flex-direction: row;
      margin-right: 71px;
      gap: 20px;
    }
  }
}

/*mixin*/
ul {
  list-style-type: none;
}

/*mixin*/
li>a {
  font-size: 16px;
  text-decoration: none;
  color: #000;
  margin-left: 80px;
}

li>a:hover {
  color: #46B1DE;
}

/*mixin*/
.logo-text,
#logoname {
  font-family: 'Sofia';
  color: #46B1DE;
}

.menu {
  .logo-text {
    #logoname {
      text-transform: uppercase;
      font-size: 38px;
      text-shadow: #000 0px 4px 4px;
    }
  }
}

.menu {
  div {
    #phone {
      color: #000;
      text-decoration: none;
      position: relative;
      top: 15px;

      &::before {
        content: "";
        position: absolute;
        top: 0;
        left: -30px;
        width: 20px;
        height: 20px;
        background-image: url('../img/-phone-in-talk_90490\ 4.png');
        background-size: contain;
      }
    }
  }
}

/*#phone::before {
  
}*/

.menu {
  .language-menu {
    position: relative;
    padding-left: 15px;
    top: 15px;
  }
}

.language-menu>span:first-child::after {
  content: "|";
  padding-left: 5px;
}

/*BURGER MENU*/
.menu {
  .header-menu__wrap {
    #burger {
      display: none;
    }

    & {
      display: none;
      justify-content: space-between;
      align-items: center;
    }
  }
}

/*.header-menu__wrap {
  
}*/

.header-menu__wrap {
  nav {
    .header__list {
      display: flex;
      flex-direction: column;
      gap: 10px;
      text-align: center;
      margin-top: 80px;
      position: relative;
      left: -150px;
    }

    & {
      .header__link {
        font-weight: 500;
        font-size: 18px;
        line-height: 23px;
        position: relative;
        color: #000;

      }
    }
  }
}

.burger__label:hover {
  cursor: pointer;
}

.header-menu__wrap {
  .burger__label {
    display: block;
    height: 26px;
    width: 32px;
    position: absolute;
    top: 55px;
    right: 20px;
    z-index: 5;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  & {
    .line-burger {
      display: block;
      height: 4px;
      width: 100%;
      border-radius: 10px;
      background: #0e2431;
    }
  }
}

  

  /*.burger__label 
}*/

  .burger__label:hover .line-burger:hover {
    background: var(--blue-);
  }

  .burger__label .line--1 {
    transform-origin: 0% 0%;
    transition: transform 0.4s ease-in-out;
  }

  .burger__label .line--2 {
    transition: transform 0.2s ease-in-out;
  }

  .burger__label .line--3 {
    transform-origin: 0% 100%;
    transition: transform 0.4s ease-in-out;
  }

  input:checked+.burger__label .line--1 {
    transform: rotate(45deg);
  }

  input:checked+.burger__label .line--2 {
    transform: scaleY(0);
  }

  input:checked+.burger__label .line--3 {
    transform: rotate(-45deg);
  }

  input~nav {
    height: 50vh;
    width: 100%;
    transform: translate(-150%);
    display: flex;
    flex-direction: column;
    margin-left: -40px;
    padding-left: 50px;
    transition: transform 0.5s ease-in-out;
    background-color: white;
    z-index: 100;
  }

  input~nav a {
    color: #fff;
    display: block;
  }

  input:checked~nav {
    transform: translateX(0);
  }

  /*MAIN SITE */

  .container {
    width: 100%;
    margin: 0 auto;
  }

  .menu-main {
    background-image: url('../img/Rectangle\ 1.jpg');
    background-repeat: no-repeat;
    padding-top: 200px;
    padding-left: 0;
    padding-right: 0;
    box-sizing: content-box;
    position: relative;
  }

  main {
    font-family: 'Roboto';
    font-weight: 400;
  }

  /*text and button*/
  h1,
  .title-blue_text {
    font-weight: 700;
    font-size: 40px;
    width: 530px;
  }

  .title-text {
    width: 464px;
    font-size: 16px;
  }

  .title-blue,
  .blue-text,
  .title-blue_text {
    color: var(--blue-);
  }

  .text {
    margin-top: 5px;
    width: 612px;
    margin: 0 auto;
  }

  .btn {
    margin-top: 48px;
  }

  .btn>a {
    box-shadow: 0 2px 10px #DDE0DC;
    border-radius: 8px;
    text-decoration: none;
  }

  .btn-tel {
    color: white;
    background-color: var(--blue-);
    margin-right: 120px;
    margin-left: 110px;
    padding: 16px 67px;
    border: 1px solid var(--blue-);
  }

  .btn-serv {
    color: var(--blue-);
    background-color: white;
    padding: 16px 46px;
    border: 1px solid white;
  }

  /*lady image and three card*/
  .lady {
    position: absolute;
    top: 100px;
    right: 100px;
  }

  .card-all {
    display: flex;
    margin-left: 20vw;
  }

  .card {
    display: flex;
    border: 1px solid white;
    width: 280px;
    height: 120px;
    background-color: white;
    margin-top: 54px;
    margin-right: 60px;
    z-index: 555;
    box-shadow: 0 4px 4px #DDE0DC;
    border-radius: 8px;
    align-items: center;
    gap: 15px;
    padding: 0 15px;
    box-sizing: border-box;
  }

  /*table*/
  .table-all {
    display: flex;
    flex-direction: column;
    margin-top: 90px;
    align-items: center;
  }

  .table-static {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 80vw;
    grid-template-rows: 2fr;
    gap: 20px;
    background-color: #A1C7D4;
    border-radius: 10px;
    padding: 16px 10px;
    justify-content: center;
    margin-top: 50px;
    margin-bottom: 90px;
  }

  .cell {
    display: flex;
    width: 25vw;
    background-color: white;
    border-radius: 10px;
    flex-direction: column;
    padding: 10px;
    box-sizing: border-box;
  }

  .all-text {
    display: flex;
    flex-direction: row;
    text-align: left;
    align-items: center;
    gap: 20px;
  }

  .text-title {
    text-transform: uppercase;
    font-size: 34px;
  }

  .text--table {
    font-size: 16px;
  }

  .number {
    font-size: 65px;
    color: var(--blue-);
  }

  .text-table {
    font-weight: 700;
  }

  /*teeth diagram*/
  .teeth {
    display: flex;
    text-align: center;
    align-items: center;
    flex-direction: column;
  }

  .teeth-circle {
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    margin-top: 50px;
  }

  .circle {
    width: 302px;
    height: 302px;
    border-radius: 50%;
    border: 1px solid black;
    position: relative;
    margin-top: 50px;
  }

  .teeth-img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .plus {
    position: absolute;
    width: 24px;
    height: 24px;
    background-color: var(--blue-);
    color: white;
    border-radius: 50%;
    top: -12px;
    left: 139px;
    transform-origin: 50% 163px;
  }

  .plus-two--left {
    transform: rotate(310deg);
  }

  .plus-three--left {
    transform: rotate(270deg);
  }

  .plus-four--left {
    transform: rotate(230deg);
  }

  .plus-two--right {
    transform: rotate(50deg);
  }

  .plus-three--right {
    transform: rotate(90deg);
  }

  .plus-four--right {
    transform: rotate(130deg);
  }

  .teeth-text--title {
    font-weight: 600;
    font-size: 20px;
  }

  .teeth-text {
    width: 340px;
    font-size: 16px;
  }

  .for-teeth-leftpart {
    display: flex;
    flex-direction: column;
    gap: 30px;
    text-align: end;
    align-items: flex-end;
  }

  .center-text {
    margin-right: 40px;
  }

  .for-teeth-rightpart {
    display: grid;
    grid-template-columns: 1;
    grid-template-rows: 3;
    text-align: start;
    margin-top: 10px;
    gap: 20px;
  }

  .teeth-center--2>div {
    padding-left: 50px;
  }

  /*atetion*/
  .atetion {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 110px;
    margin-top: 60px;
  }

  /*data user*/
  .user-data {
    margin-top: 87px;
    border: 1px solid #D9D9D9;
    border-radius: 10px;
    margin: 0 auto;
    width: 850px;
    padding: 10px;
    align-items: center;
  }

  .data-title {
    text-transform: uppercase;
    font-weight: 500;
    font-size: 32px;
    padding-top: 29px;
    padding-left: 25px;
  }

  .container-btn {
    display: flex;
    flex-direction: column;
    height: 250px;
    width: 820px;
    border: 1px solid #D9D9D9;
    align-items: center;
    border-radius: 10px;
    padding: 10px;
  }

  .user-btn {
    display: flex;
    flex-direction: row;
    gap: 30px;
    padding-bottom: 46px;
    padding-top: 44px;
  }

  .one-btn {
    display: flex;
    width: 230px;
    height: 57px;
    flex-direction: row;
    background-color: #D9D9D9;
    border-radius: 8px;
  }

  .one-btn:has(input:focus) {
    border: 2px solid #46B1DE;
  }

  button {
    border-color: #D9D9D9;
    border: 1px solid #D9D9D9;
  }

  .blue-btn {
    width: 180px;
    background-color: var(--blue-);
    justify-content: center;
    align-items: center;
    color: white;
  }

  .sign {
    background-color: var(--blue-);
    border: 1px solid var(--blue-);
  }

  .text-user {
    font-size: 19px;
    font-weight: 500;
    color: #D9D9D9;
    padding: 5px;
    border: none;
    background-color: #D9D9D9;
    width: 150px;
    border-radius: 8px;
  }

  .one-btn>img:first-child {
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 16px;
  }

  .line {
    padding: 13px;
  }

  /*foto galery*/
  .gallery {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 90px;
    align-items: center;
  }

  .foto {
    width: 280px;
    height: 128px;
    border-radius: 8px;
    margin-right: 70px;
  }

  /*ANIMATION*/

  .card-all {
    animation: CardAll 3s 1;
    transition-timing-function: ease-in;
  }

  h1,
  .title-blue_text {
    animation: MainText 0.5s 1;
  }

  .title-text,
  .title-blue_text,
  .btn {
    animation: MainText 1s 1;
  }

  @keyframes CardAll {
    from {
      transform: translateX(-5vw);
      opacity: 0;
    }

    to {
      transform: translateX(0);
      opacity: 1;
    }
  }

  @keyframes MainText {
    from {
      transform: translateY(5vw);
      opacity: 0;
    }

    to {
      transform: translateY(0);
      opacity: 1;
    }
  }

  /*ANIMATION FOR TEETH*/

  .plus {
    animation-name: plusTeeth;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
    animation-direction: alternate-reverse;
  }

  @keyframes plusTeeth {
    from {
      transform-origin: 50% 163px;
    }

    to {
      transform-origin: 50% 12px;
      top: 140px;
    }
  }

  .teeth-center--1,
  .teeth-center--2,
  .teeth-bottom--1,
  .teeth-bottom--2 {
    animation-name: textForTeeth;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
    animation-direction: alternate;
  }

  .teeth-top--1,
  .teeth-top--2 {
    animation-name: textForTeethUp;
    animation-duration: 2s;
    animation-timing-function: linear;
    animation-fill-mode: forwards;
    animation-direction: alternate;
  }

  @keyframes textForTeethUp {
    0% {
      margin-top: -50px;
    }

    50% {
      margin-top: -25px;
    }

    100% {
      margin-top: 0px;
    }

  }

  @keyframes textForTeeth {
    0% {
      padding: 50px;
    }

    50% {
      padding: 25px;
    }

    100% {
      padding: 0px;
    }

  }

