    }

    .table-all {
      text-align: left;
      margin-left: 1vw;
    }

    .text-title {
      text-transform: uppercase;
      font-size: 20px;
      width: 480px;
      margin-left: 0;
    }

    .text {
      margin-left: 0;
      width: 300px;
    }

    .user-data {
      display: none;
    }

    .card-all {
      align-items: flex-start;
      margin-left: 10vw;
      margin-top: 180px;
    }

    .table-static {
      display: flex;
      flex-direction: column;
      background-color: #A1C7D4;
      border-radius: 10px;
      padding: 16px 20px;
      margin-top: 50px;

    }

    .cell {
      width: 70vw;
    }

    .teeth-circle {
      margin: 0 auto;
      display: flex;
      flex-direction: column;
    }

    .for-teeth-leftpart {
      text-align: center;
    }

    .center-text {
      margin-right: 0px;
    }

    .teeth-center--2>div {
      padding-left: 0;
    }

    .atetion {
      display: flex;
      flex-direction: column;
    }

    .gallery {
      flex-direction: column;
    }

    .foto {
      margin-bottom: 15px;
    }

    .teeth-top--1,
    .teeth-top--2 {
      animation: none;
    }

    .teeth-center--1,
    .teeth-center--2,
    .teeth-bottom--1,
    .teeth-bottom--2 {
      animation: none;
    }
